.popup-zoom.off {
	display: none;
}

.popup-zoom {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	top: 0;
	left: 0;

	// .popup-material__body
	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	// .popup-material__img
	&__img {
		position: relative;
		max-width: 500px;
		width: 500px;
		height: 100%;
		padding: 30px;

		img {
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}

	// .popup-material__close
	&__close {
		cursor: pointer;
		position: absolute;
		top: 20px;
		right: 20px;
		display: block;
		width: 25px;
		height: 25px;
		background: url('../../img/popup/close.png') 0 0/cover no-repeat;
	}
}

