.popup-form.off {
	display: none;
}

.popup-form {
		overflow: auto;
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: #fff;
		top: 0;
		left: 0;
		padding: 0 0 40px 0;
	padding: 10px 0 80px;
	// .popup-form__close
	&__close {
		position: relative;
    width: 30px;
    height: 20px;
    margin: 20px 20px 10px auto;
		cursor: pointer;
		&:after, &:before {
			content: "";
			display: block;
			position: absolute;
			height: 4px;
			width: 100%;
			background-color: #000;
		}

		&:before {
			transform: rotate(45deg);
		}

		&:after {
			transform: rotate(-45deg);
		}
	}
	// .popup-form__content
	&__content {
		@media (max-width: $md1+px) {
			max-width: 100% !important;
			padding: 0 !important;
		}
	}
	// .popup-form__details
	&__details {}
	// .popup-form__person
	&__person {}
	// .popup-form__btn-send
	&__btn-send {
		cursor: pointer;
		text-align: center;
		line-height: 50px;
		font-size: 17px;
		font-family: "HeliosCondBold";
		border: 1px solid #000;
		height: 50px;
		width: 180px;
		margin: 0 0 0 7%; 
		color: #fff;
		background-color: #000;
		transition: 0.2s all;
		text-transform: uppercase;
	}
}

.details-popup-form-head {
	padding: 0 2%;
	color: #000;
	font-size: 22px;
	font-family: "HeliosCondBold";
	font-weight: 600;
	line-height: 45px;
	width: 100%;
	height: 45px;
	border: 1px solid #ddd;
	border-left: none;
	border-right: none;
	background-color: #737c8117;
}

.details-popup-form-body {
	display: flex;
	@media (max-width: $md3+px) {
		flex-wrap: wrap;
		justify-content: center;
	}
	// .details-popup-form-body__img
	&__img {
		width: 350px;
		margin: 0.5%;
		@media (max-width: $md3+px) {
			flex: 1 1 100%;
			max-width: 90%;
		}
		img {
			margin: 2% 2%;
			width: 100%;
			@media (max-width: $md3+px) {
				display: block;
				margin: 2% auto;
				width: 90%;
			}
		}
	}
	// .details-popup-form-body__line
	&__line {
		display: none;
		width: 100%;
		height: 45px;
		border: 1px solid #ddd;
		border-left: none;
		border-right: none;
		background-color: #737c8117;
		@media (max-width: $md3+px) {
			display: block;
		}
	}
	// .details-popup-form-body__info
	&__info {
		flex: 1 1 auto;
		padding: 20px 30px;
		span {
			line-height: 40px;
			font-size: 17px;
			font-weight: bold;
			display: inline-block;
		}
	}
	// .details-popup-form-body__article
	&__article {}
	// .details-popup-form-body__title
	&__title {
		font-family: 'ConvCleanvertising';
		margin: 0 5px 0 0;
	}
	//details-popup-form-body__separator
	&__separator {
		font-family: 'ConvCleanvertising';
		margin: 0 5px;
	}
	//details-popup-form-body____сolons
	&__сolons {
		font-family: 'ConvCleanvertising';
		margin: 0 5px 0 0;
	}
	// .details-popup-form-body__num
	&__num {}
	// .details-popup-form-body__quantity
	&__quantity {
		span:nth-child(1) {
			@media (max-width: 380px) {
				width: 100%;
			}
		}
	}
	// .details-popup-form-body__q
	&__q {
		margin: 0 5px 0 0;
	}
	// .details-popup-form-body__price
	&__price {
		margin: 0 5px 0 0;
	}
	// .details-popup-form-body__angle
	&__angle {
		span:nth-child(1) {
			@media (max-width: 380px) {
				width: 100%;
			}
		}
	}
	// .details-popup-form-body__trowelling
	&__trowelling {
		.details-popup-form-body__сolons {
			margin: 0 5px 0 0;
		}
	}
	// .details-popup-form-body__glue
	&__glue {}
	// .details-popup-form-body__impregnation
	&__impregnation {}
}

.details-popup-form-footer {
	padding: 0 5%;
	color: #000;
	font-size: 19px;
	font-family: "HeliosCondBold";
	font-weight: 600;
	line-height: 45px;
	width: 100%;
	height: 45px;
	border: 1px solid #ddd;
	border-left: none;
	border-right: none;
	background-color: #737c8117;
	span:nth-child(1) {
		margin: 0 30px 0 0;
	}
	span:nth-child(2) {
		margin: 0 5px 0 0;
	}
}

