	// .person-popup-form__title
	&__title {
		font-size: 22px;
		font-family: "HeliosCondBold";
		text-transform: uppercase;
		margin: 2% 0 0;
	}
	// .person-popup-form__block-mail
	&__block-mail {}
	//.person-popup-form__block
		&__block {
		display: flex;
		flex-wrap: wrap;
		margin: 25px 0 0 0;

		input,
		textarea {
