body {
	color: #000;
	font-size: 14px;
	&._lock {
		overflow: hidden;
	}
}

._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;
	@media (max-width: $md1+px) {
		max-width: 970px;
	}
	@media (max-width: $md2+px) {
		max-width: 750px;
	}
	@media (max-width: $md3+px) {
		max-width: none;
		padding: 0 10px;
	}
}

.page {
	flex: 1 1 auto;
}

//===================================================================================================================================
 
.preview {
	// .preview__content
	&__content {
		padding: 25px 0 10px;
	}
	// .preview__row
	&__row {
		display: flex;
		justify-content: center;
		@media (max-width: $md3+px) {
			flex-wrap: wrap;
		}
	}
	// .preview__img
	&__img {
		width: 35%;
		@media (max-width: $md1+px) {
			width: 40%;
			margin: 0 0 0 5%;
		}
		@media (max-width: $md2+px) {
			width: 40%;
			margin: 0;
		}
		@media (max-width: $md3+px) {
			width: 100%;
			margin: 15px auto;
			padding: 0 15px;
		}
		img {
			object-fit: fill;
			width: 100%;
			height: auto;
		}
	}
	// .preview__block
	&__block {
		width: 50%;
		margin: 0 0 0 5%;
		@media (max-width: $md1+px) {
			width: 45%;
		}
		@media (max-width: $md3+px) {
			width: 100%;
			margin: 15px auto;
			padding: 0 15px;
		}
	}
	// .preview__title
	&__title {
		font-family: "HeliosCondBold";
		font-weight: 100;
		font-size: 30px;
		text-transform: uppercase;
	}
	// .preview__text
	&__text {
		font-size: 15px;
    font-weight: 600;
    line-height: 1.5;
		font-weight: 100;
		margin: 20px 0 0;
	}
	// .preview__colors
	&__colors {}
}


.colors-preview {
	margin: 40px 0 0;
	&__title {
		font-size: 26px;
		font-weight: 300;
		font-family: "HeliosCondBold";
		text-transform: uppercase;
	}
	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		margin: 15px 0 0;
	}
	// .colors-preview__item
	&__item {
		margin: 10px 20px 10px 0;
		&:nth-child(n+11){ 						// (n + Указать количество элементов + 1)
			display: none;
		}
	}
	// .colors-preview__img
	&__img {
		width: 100px;
		height: 100px;
		cursor: pointer;
		img {
			width: 100%;
			height: 100%;
			cursor: zoom-in;
		}
	}
	// .colors-preview__checkmark
	&__checkmark {
		margin: 10px 0 0;
		display: flex;
	}
	// .colors-preview__numer
	&__numer {
		display: block;
		font-size: 22px;
		text-transform: uppercase;
		font-family: "HeliosCondBold";
		margin: 0 10px 0 0;
	}
	// .colors-preview__border
	&__border {
		position: relative;
		display: block;
		width: 20px;
		height: 20px;
		border: 2px solid #000;
		cursor: pointer;

		&._active {

			&:before,
			&:after {
				content: "";
				display: block;
				position: absolute;
				background: #000;
				transform: rotate(45deg);
			}

			&:before {
				width: 7.2px;
				height: 2.5px;
				top: 8px;
				left: 1.75px;
			}

			&:after {
				width: 2.5px;
				height: 9.9px;
				top: 3.45px;
				left: 8.6px;
			}
		}
	}
}

//===================================================================================================================================
 
.order {
	padding: 20px 0 0;
	// .order__content
	&__content {}
	// .order__title
	&__title {
		text-decoration: underline;
		font-size: 22px;
    color: #000;
    font-family: "HeliosCondBold";
    text-transform: uppercase;
	}
	// .order__row
	&__row {
    display: flex;
    justify-content: space-between;
		align-items: flex-start;
		margin: 20px 0;
		@media (max-width: $md3+px) {
			justify-content: center;
			flex-wrap: wrap;
		}
	}
	// .order__img
	&__img {
		width: 60%;
		@media (max-width: $md2+px) {
			width: 50%;
		}
		@media (max-width: $md3+px) {
			width: 100%;
		}
		img {
			width: 100%;
			height: auto;
			border: 1px dotted #000;
			object-fit: cover;
		}
	}
	// .order__settings
	&__settings {
		padding: 20px;
    box-shadow: 0px 17px 56px -3px #8c8787;
		width: 30%;
    margin: 0 5%;
		@media (max-width: $md2+px) {
			width: 50%;
		}
		@media (max-width: $md3+px) {
			width: 100%;
			margin: 30px 5%;
		}
	}
}

.settings-order {
	// .settings-order__title
	&__title {
		text-align: center;
    color: #000;
    font-size: 18px;
    font-family: "HeliosCondBold";
    text-transform: uppercase;
	}
	// .settings-order__text
	&__text {
		font-family: "HeliosCondBold";
    font-size: 12px;
		font-weight: 400;
	}
	// .settings-order__size
	&__size {}
	
	&__angular {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	// .settings-order__trowelling
	&__trowelling {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		margin: 20px 0 0;
	}
	// .settings-order__glue
	&__glue {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 20px 0 0;
	}
	// .settings-order__impregnation
	&__impregnation {
		// display: flex;
		// align-items: center;
		// justify-content: space-between;
		// margin: 20px 0 0;
	}
	// .settings-order__explanations
	&__explanations {
		font-family: "HeliosCondBold";
    font-size: 12px;
		font-weight: 400;
	}
	// .settings-order__sum
	&__sum {
		font-size: 20px;
    font-weight: 800;
    color: #000;
	}
	// .settings-order__buy-btn
	&__buy-btn {
		color: #fff;
    font-size: 17px;
    line-height: 48px;
    font-family: "HeliosCondBold";
    text-align: center;
    text-transform: uppercase;
    height: 50px;
    width: 100%;
    background: #000;
    padding: 0 20px;
    margin: 20px 0 0px;
    cursor: pointer;
	}
	// .settings-order__other-btn
	&__other-btn {
		color: #000;
    display: block;
    text-align: center;
    height: 50px;
    width: 100%;
    font-family: "HeliosCondBold";
    font-size: 17px;
    line-height: 48px;
    text-transform: uppercase;
    border: 1px solid #000;
    padding: 0 20px;
    margin: 20px 0 0 0;
    cursor: pointer;
    transition: 0.2s all;
	}
}


.size-settings-order {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	// .size-settings-order__width
	&__width {
		display: none;
		&._active{
			display: block;
			width: 47%;
			margin: 10px 0 0;
			&:nth-child(1) {
				margin: 0;
			}
		}
	}
	// .size-settings-order__text
	&__text {
		span {
			font-weight: bold;
		}
	}
	// .size-settings-order__input
	&__input {
		width: 100%;
    height: 35px;
    border: 1px solid #cacaca;
    margin: 5px 0 0;
    padding: 12px;
	}
	// .size-settings-order__hight
	&__hight {
		display: none;
		&._active{
			display: block;
			width: 47%;
			margin: 10px 0 0;
			&:nth-child(2) {
				margin: 0;
			}
		}
	}
	&__add-or-del {
		display: none;
		&._active {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			margin: 10px 0 0;
			> div {
				margin: 0 10px;
			}
			img {
				width: 20px;
				height: 20px;
				cursor: pointer;
			}
		}
		&__del {
			img {
				width: 18px !important;
				height: 18px !important;
			}
		}
	}
}

.angular-settings-order {
	// .angular__text
	&__text {
		width: 47%;
		margin: 0 4% 0 0;
		span {
			font-weight: bold;
		}
	}
	// .angular__input
	&__input {
		// width: 47%;
    // margin: 5px 0 0;
	}
}


.angular-settings-order {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	overflow: hidden;
	// .angular-settings-order__title
	&__title {
		line-height: 1.3;
		span {
			font-weight: bold;
		}
		@media (max-width: $md4+px) {
			width: 60%;
		}
	}
	&__subtext {
		font-family: "HeliosCondBold";
    font-size: 12px;
	}
	// .angular-settings-order__checkbox
	&__checkbox {}
	// .angular-settings-order__checkbox-input
	&__checkbox-input {}
	// .angular-settings-order__input
	&__input {
		width: 100%;

		visibility: hidden;
		opacity: 0;
		height: 0;

		transition: 0.75s;
		pointer-events: none;
		input {
			width: 100%;
			height: 100%;
			border: 1px solid #cacaca;
			padding: 12px;
		}
		&._active {
			visibility:	visible;
			opacity: 1;
			height: 35px;
			margin: 12px 0 0;

			transition: 0.75s;
			pointer-events: all;
		}
	}
}

.trowelling-settings-order {
	// .trowelling-settings-order__title
	&__title {}
	// .trowelling-settings-order__checkbox
	&__checkbox {}
	// .trowelling-settings-order__checkbox-input
	&__checkbox-input {}
	// .trowelling-settings-order__row
	&__row {
		flex: 1 1 auto;
		display: flex;
		justify-content: space-between;
		width: 100%;

		overflow: hidden;
		height: 0;
		opacity: 0;

		transition: 0.75s;

		&._active {
			overflow:	visible;
			height: 100%;
			opacity: 1;
			margin: 20px 0 0;

			transition: 0.75s;
			pointer-events: all;
		}
	}
	// .trowelling-settings-order__item
	&__item {}
}

